import {
	uniIcons,
	uniPopup ,
	uniNavBar
} from '@dcloudio/uni-ui';
import simpleDatetimePicker from '@buuug7/uniapp-components/lib/simple-datetime-picker';

export default {
	components: {
		uniIcons,
		uniNavBar,
		uniPopup ,
		simpleDatetimePicker
	},
	data(){
		return{
			ismodel:false,
			ismolnum:1,
			paytype:[],
			types: {
				collect: [],
				branch: []
			},
			dataobj:{
				genre:1,
				amount:"",
				useDate:"",
				payType:{},
				useType:{},
				remarks:""
			},
			typeitems:[]
		}
	},
	onLoad: function(option) {
		this.paytype={ ...this.GlobalJson.paytype };
		let _types = { ...this.GlobalJson.expenditure };
		for (let i in _types) {
			if (_types[i].type == 1) {
				this.types.collect.push(_types[i]);
				
			} else if (_types[i].type == 2) {
				this.types.branch.push(_types[i]);
			}
		}
		this.typeitems= this.types.collect;
		if(option.item){
			let _dataobj = JSON.parse(option.item);
			this.dataobj=_dataobj
		}else{
			this.dataobj.useDate=this.utils.Format(new Date());
		}
	},
	methods: {
		//返回上一页面
		toback() {
			uni.navigateBack({
				delta: 1
			});
		},
		//切换收入支出
		handleexin(val){
			this.$refs.popup.close();
			if(this.dataobj._id){
				this.dataobj.genre=val;
				this.dataobj.useType={};
			}else{
				let _Obj={
					genre:val,
					amount:"",
					useDate:"",
					payType:{},
					useType:{},
					remarks:""
				};
				_Obj.useDate=this.utils.Format(new Date());
				this.dataobj=_Obj;
			}
			
			
			if(val==1){
				this.typeitems= this.types.collect;
			}else{
				this.typeitems=this.types.branch;
			}
		},
		// 弹出模态框 
		showModel(val){
			this.ismolnum=val;
			this.$refs.popup.open()
		},
		// 保存数据
		saveData(){
			  uni.showLoading({
			    title: '数据保存中...',
				mask:true
			  })
			  let _data={...this.dataobj},eventName='',_this=this;
			  _data.useDategetTime=new Date(_data.useDate).getTime()
			  if(_data._id){
				  eventName="update";
				  _data.changeTime=Date.now();
				  _data.dataId=_data._id;
				  delete _data._id;
			  }else{
				  eventName="add";
				  _data.createTime=Date.now();
			  }
			  uniCloud.callFunction({
			    name: eventName,
			    data: _data
			  }).then((res) => {
			    uni.hideLoading()
			    uni.showToast({
			    	title:'数据保存成功',
					mask:true,
					duration:2000
			    })
				if(_data.dataId){
					_data._id=_data.dataId;
					delete _data.dataId;
					try {
					    uni.setStorageSync('editData', _data);
					} catch (e) {
					    // error
					}
				}
				setTimeout(()=>{
					_this.toback();
				},2500)
			  }).catch((err) => {
			    uni.hideLoading()
			    uni.showToast({
			    	title:'数据保存失败，请重新保存',
					mask:true,
			    	duration:2000
			    })
			    console.error(err)
			  })
		},
		//选择某个pay方式 
		handlepay(item){
			this.dataobj.payType=item;
			this.$refs.popup.close();
		},
		// 选择某个类别
		handleType(item){
			this.dataobj.useType=item;
			this.$refs.popup.close();
		},
		// 打开picker
		openDatetimePicker() {
			this.$refs.myPicker.show();
		},
		// 关闭picker
		closeDatetimePicker() {
			this.$refs.myPicker.hide();
		},
		// 确定选择的日期时间
		handleSubmit(e) {
			//console.log(e); // {year: "2019", month: "07", day: "17", hour: "15", minute: "21"}
			if (e.year && e.month && e.day) {
				this.dataobj.useDate = `${e.year}-${e.month}-${e.day} ${e.hour}:${e.minute}`;
			}
		}
	}
}
